pre.highlight {
    position: relative;
    margin: 4px;
    padding: 8px;
    background: #000;
    border: 2px solid #555;
    box-shadow: 0 0 5px #fff;
    color: #ccc;
    border-radius: .5em;
    overflow-x: auto;
}

pre.highlight.js::after {
    content: "Javascript";
    position: absolute;
    top: 0;
    right: 0;
    padding: 4px;
    font-variant: small-fonts;
    opacity: .3;
    color: #fff;
}

pre.highlight .keyword {
    color: #07f;
    font-weight: bold;
}

pre.highlight .keyword2 {
    color: #0ff;
    font-weight: bold;
}

pre.highlight .function {
    color: #fff;
    font-weight: bold;
}

pre.highlight .symbol {
    color: #f08;
}

pre.highlight .operator {
    color: #f80;
}

pre.highlight .comment {
    color: #777;
}

pre.highlight .string {
    color: #0f0;
}

pre.highlight .regexp {
    background: #ff8;
    color: #000;
}

