/* See https://github.com/fred-wang/mathml.css */
   
@namespace "http://www.w3.org/1998/Math/MathML";

/* math */
div.math {
    display: inline;
    text-indent: 0;
}
div.math[display="block"] {
    display: block;
    text-align: center;
}

/* fraction */
div.math mfrac {
    display: inline-block !important;
    vertical-align: -50%;
    border-collapse: collapse;
}
div.math mfrac > * {
    display: block !important;
}
div.math mfrac > * + * {
    display: inline-block !important;
    vertical-align: top;
}
div.math mfrac:not([linethickness="0"]) > *:first-child {
    border-bottom: solid thin;
}

/* sub/sup scripts */
div.math msub > *:nth-child(2), msubsup > *:nth-child(2),
div.math mmultiscripts > *:nth-child(2n+2),
div.math mmultiscripts > mprescripts ~ *:nth-child(2n+3) {
    font-size: 0.8em;
    vertical-align: sub;
}
div.math msup > *:nth-child(2), msubsup > *:nth-child(3),
div.math mmultiscripts > *:nth-child(2n+3),
div.math mmultiscripts > mprescripts ~ *:nth-child(2n+2) {
    font-size: 0.8em;
    vertical-align: super;
}
div.math mprescripts:after {
    content: ";";
}

/* under/over scripts */
div.math munder,
div.math mover,
div.math munderover {
    display: inline-flex !important;
    flex-direction: column;
}
div.math munder > *:nth-child(2),
div.math munderover > *:nth-child(2) {
    font-size: 0.8em;
    order: +1;
}
div.math mover > *:nth-child(2),
div.math munderover > *:nth-child(3) {
    font-size: 0.8em;
    order: -1;
}
div.math munder {
    vertical-align: text-top;
}
div.math mover {
    vertical-align: text-bottom;
}
div.math munderover {
    vertical-align: middle;
}

/* roots */
div.math msqrt,
div.math mroot {
    display: inline-flex !important;
    margin-left: .5em;
    vertical-align: middle;
    border-top: solid thin;
}
div.math msqrt:before,
div.math mroot:before {
    margin-left: -.5em;
    content: "\221A";
}
div.math mroot > *:nth-child(2) {
    margin-right: .25em;
    margin-left: -.75em;
    font-size: 0.8em;
    order: -1;
}

/* menclose */
div.math menclose {
  display: inline-table !important;
  border-collapse: separate;
  border-spacing: 0.4ex 0;
}
div.math menclose[notation*="top"],
div.math menclose[notation*="actuarial"] {
  border-top: solid thin;
}
div.math menclose[notation*="bottom"],
div.math menclose[notation*="madruwb"] {
    border-bottom: solid thin;
}
div.math menclose[notation*="right"],
div.math menclose[notation*="actuarial"],
div.math menclose[notation*="madruwb"] {
    border-right: solid thin;
}
div.math menclose[notation*="left"] {
    border-left: solid thin;
}
div.math menclose[notation*="box"],
div.math menclose[notation*="roundedbox"],
div.math menclose[notation*="circle"] {
    border: solid thin;
}
div.math menclose[notation*="roundedbox"] {
    border-radius: 15%;
}
div.math menclose[notation*="circle"] {
    border-radius: 50%;
}
div.math menclose[notation*="horizontalstrike"] {
    text-decoration: line-through;
}

/* table */
div.math mtable {
    display: inline-table !important;
    vertical-align: middle;
}
div.math mtr {
    display: table-row !important;
}
div.math mtd {
    display: table-cell !important;
    padding: 0 0.5ex;
}

/* token elements */
div.math mspace {
    margin: .2em;
}
div.math mi {
    font-style: italic;
}
div.math mo {
    margin-right: .2em;
    margin-left: .2em;
}
div.math ms:before,
div.math ms:after {
    content:"\0022";
}
div.math ms[lquote]:before {
    content: attr(lquote);
}
div.math ms[rquote]:after {
    content: attr(rquote);
}

/* mathvariants */
div.math [mathvariant="bold"],
div.math [mathvariant="bold-italic"],
div.math [mathvariant="bold-sans-serif"],
div.math [mathvariant="sans-serif-bold-italic"] {
    font-weight: bold;
    font-style: normal;
}
div.math [mathvariant="monospace"] {
    font-family: monospace;
    font-style: normal;
}
div.math [mathvariant="sans-serif"],
div.math [mathvariant="bold-sans-serif"],
div.math [mathvariant="sans-serif-italic"],
div.math [mathvariant="sans-serif-bold-italic"] {
    font-family: sans-serif;
    font-style: normal;
}
div.math [mathvariant="italic"],
div.math [mathvariant="bold-italic"],
div.math [mathvariant="sans-serif-italic"],
div.math [mathvariant="sans-serif-bold-italic"] {
    font-style: italic;
}
div.math [mathvariant="normal"] {
    font-style: normal;
}

/* mphantom */
div.math mphantom {
    visibility: hidden;
}

/* merror */
div.math merror {
    outline: solid thin red;
}
div.math merror:before {
    content: "Error: ";
}

/* annotations */
div.math semantics > *:first-child {
    display: inline;
}
div.math annotation,
div.math annotation-xml {
    font-family: monospace;
    display: none !important;
}
div.math math:active > semantics > *:first-child,
div.math math:active > semantics > *:first-child {
    display: none !important;
}
div.math math:active annotation:first-of-type {
    display: inline !important;
}

.tfw-grader {
    padding: 1em;
    border-radius: 1em;
    border: 4px dashed rgba(0,0,0,.5);
}

.tfw-grader > pre {
    margin: 0;
}


.tfw-grader > pre.error {
    margin: 1em 0 0 0;
    color: #f00;
    font-size: 80%;
    line-height: 100%;
}

.tfw-grader > div.ok {
    display: inline-block;
    color: #0e0;
    text-shadow: 1px 1px 0 #000;
    font-weight: bolder;
    margin: 0 0 0 2em;
    transform: translateX(5em);
    opacity: 0;
}

.tfw-grader > div.ok.show {
    transition: all .3s;
    transform: translateX(0);
    opacity: 1;
}

*.wdg {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
.depiler2bouts-main {
    display: inline-block;
    border: 1em;
    background: #fff;
    border-radius: 1em;
    margin: 1em;
    padding: 1em;
}

.depiler2bouts-main > div.piece {
    position: relative;
    display: inline-block;
    margin: 0;
    padding: 0;
    width: 40px;
    height: 32px;
    overflow: visible;
    cursor: pointer;
    transition: transform .3s;
    transform: scaleX(1);
}

.depiler2bouts-main > div.piece > div {
    position: absolute;
    width: 32px;
    height: 32px;
    line-height: 32px;
    font-size: 20px;
    font-weight: bolder;
    border: 1px solid #333;
    text-align: center;
    border-radius: 100%;
    background: #ffb;
    overflow: hidden;
}

.depiler2bouts-main > div.piece.flip {
    transition: transform .3s;
    transform: scaleX(0);
}

.depiler2bouts-main > div.piece > div.you {
    background: #0f0;
}

.depiler2bouts-main > div.piece > div.him {
    background: #f00;
}

.depiler2bouts-main > div.piece > div::after {
    content: "";
    position: absolute;
    left: -10px;
    top: -10px;
    width: 100%;
    height: 100%;
    border-radius: 100%;
    background: rgba(255,255,255,.5);
}


.depiler2bouts-main > div.piece::before {
    content: "";
    position: absolute;
    display: block;
    left: 4px;
    top: 1px;
    width: 32px;
    height: 32px;
    border-radius: 100%;
    background-color: rgba(0, 0, 0, 0.25);
    box-shadow: 0px 0px 1px rgba(0, 0, 0, 0.25);
    transform-origin: 50% 100% 0px;
    transform: scaleY(0.4);
}

.depiler2bouts-main > div.tbl {
    display: table;
    width: 100%;
    margin: 16px 0 0 0;
    border-collapse: collapse;
}

.depiler2bouts-main > div.tbl > div {
    display: table-cell;
    text-align: center;
    vertical-align: middle;
    width: 33.33%;
}

.depiler2bouts-main > div.tbl .pts1,
.depiler2bouts-main > div.tbl .pts2 {
    font-size: 48px;
    font-bold: bolder;
    text-shadow: 2px 2px 1px rgba(0,0,0,.5);
}

.depiler2bouts-main > div.tbl .pts1 {
    color: #0f0;
}

.depiler2bouts-main > div.tbl .pts2 {
    color: #f00;
}

.depiler2bouts-main a.button,
.depiler2bouts-main a.button:hover,
.depiler2bouts-main a.button:visited {
    display: inline-block;
    text-decoration: none;
    height: 30px;
    font-size: 14px;
    padding: 0 15px;
    border-radius: 15px;
    background: #9cd;
    color: #000;
}


.depiler2bouts-main div.delta {
    font-weight: 300;
    font-size: 12px;
    color: #000;
    margin: .5em 0 0 0;
}

.depiler2bouts-main div.delta.you {
    color: #0f0;
    font-weight: bolder;
}

.depiler2bouts-main div.delta.him {
    color: #f00;
    font-weight: bolder;
}

div.tp4-bulle-container {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    margin: 0;
    background-color: rgba(0,0,0,.7);
    z-index: 100001;
}

div.tp4-bulle-container a.tp4-button.simple {
    color: #579;
}

div.tp4-bulle {
    position: absolute;
    margin: 0;
    padding: 0;
    opacity: 0;
    transition: 1.5s opacity;
    overflow: visible;
}

div.tp4-bulle > div.frame {
    width: 240px;
    position: absolute;
    left: 0;
}

div.tp4-bulle.top > div.frame {
    top: 0;
}

div.tp4-bulle.bottom > div.frame {
    bottom: 0;
}


div.tp4-bulle.show {
    opacity: 1;
    transition: 1.5s opacity;
}

div.tp4-bulle div.svg {
    position: relative;
    width: 240px;
    height: 40px;
    background-repeat:   no-repeat;
    background-position: top left;
    background-size:     240px 40px;
}

div.tp4-bulle.bottom div.svg {
    background-position: bottom left;
}

div.tp4-bulle.left div.svg {
    background-image:    url(tp4.bulle/tl.svg);
}

div.tp4-bulle.right div.svg {
    background-image:    url(tp4.bulle/tr.svg);
}

div.tp4-bulle.left.bottom div.svg {
    background-image:    url(tp4.bulle/bl.svg);
}

div.tp4-bulle.right.bottom div.svg {
    background-image:    url(tp4.bulle/br.svg);
}

div.tp4-bulle div.content {
    position: relative;
    margin: 0;
    width: 220px;
    padding: 10px;
    border-top: none;
    border-left: 1px solid #000;
    border-right: 1px solid #000;
    border-bottom: 1px solid #000;
    background-color: #eee;
    color: #333;
    font-size: 10pt;
    font-family: sans-serif;
}

div.tp4-bulle.bottom div.content {
    border-top: 1px solid #000;
    border-left: 1px solid #000;
    border-right: 1px solid #000;
    border-bottom: none;
}

div.tp4-bulle.left div.content {
    margin-left: 20px;
}
